* {
  box-sizing: border-box; }

:local(.mainContainer) {
  padding: 40px 52px 24px 52px;
  max-width: 1080px; }
  @media screen and (max-width: 768px) {
    :local(.mainContainer) {
      padding-left: 32px;
      padding-right: 32px; } }
  @media screen and (max-width: 960px) {
    :local(.mainContainer) {
      padding-top: 80px; } }

:local(.flexContainer) {
  padding-left: 248px; }
  @media screen and (max-width: 960px) {
    :local(.flexContainer) {
      padding-left: 0px; } }

:global(.noMargin) {
  margin: 0px; }

:local(.pageHeading) {
  size: 65px; }

:global(.spectrum-Table-row) {
  cursor: auto; }
  :global(.spectrum-Table-row):hover {
    background-color: inherit !important;
    cursor: auto; }

p:global(.spectrum-Body3) {
  margin-bottom: 24px;
  margin-top: 0px; }

:global(.spectrum-Body3) {
  margin-bottom: 8px; }

:global(.well-box) {
  background-color: rgba(45, 45, 45, 0.02);
  border: 1px solid rgba(45, 45, 45, 0.05);
  padding: 15px 16px;
  border-radius: 4px;
  display: inline-block;
  margin-top: 0px;
  margin-bottom: 24px;
  font-size: 16px; }
